<template lang="pug">
#sidebar(@click="closeMenu")
  #menu(@click="menuClicked")
    .close
      .icon(@click="closeMenu")
        svg.bi.bi-x(
          width="32px",
          height="32px",
          viewBox="0 0 16 16",
          fill="#c7e4e9",
          xmlns="http://www.w3.org/2000/svg"
        )
          path(
            fill-rule="evenodd",
            d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
          )
    .nav-item(
      :class="{ 'selected-item': selectedItem === 'about' }",
      @click="goTo('about')"
    )
      p About
    .nav-item(
      :class="{ 'selected-item': selectedItem === 'work' }",
      @click="goTo('work')"
    )
      p Work
    .nav-item(
      :class="{ 'selected-item': selectedItem === 'project' }",
      @click="goTo('project')"
    )
      p Projects
    .nav-item(
      :class="{ 'selected-item': selectedItem === 'accolades' }",
      @click="goTo('accolades')"
    )
      p Accolades
    .nav-item(
      :class="{ 'selected-item': selectedItem === 'contact' }",
      @click="goTo('contact')"
    )
      p Contact
    .resume-container
      .resume(@click="openResume")
        p Resume
</template>

<script>
export default {
  methods: {
    openResume() {
      const a = document.createElement("a");
      a.href = "/resume.pdf";
      a.target = "_blank";
      a.click();
    },
    closeMenu() {
      const el = document.getElementById("menu");
      el.style.transform = "translateX(-200px)";
      setTimeout(() => {
        this.$store.commit("setShowMobileMenu", false);
      }, 250);
    },
    menuClicked(e) {
      e.stopPropagation();
    },
    goTo(id) {
      this.$store.commit("setNavSection", id);
      const el = document.getElementById(id);

      if (this.selectedItem === "contact") {
        el.scrollIntoView({
          behaviour: "smooth",
          block: "end",
        });
      } else {
        el.scrollIntoView({
          behaviour: "smooth",
          block: "start",
        });
      }
      this.closeMenu();
    },
  },
  computed: {
    selectedItem() {
      return this.$store.state.navSection;
    },
  },
  mounted() {
    setTimeout(() => {
      const el = document.getElementById("menu");
      el.style.transform = "translateX(0px)";
    }, 100);
  },
};
</script>

<style lang="sass" scoped>
#sidebar
  position: fixed
  width: 100%
  height: 100vh
  z-index: 1500
  top: 0%
  left: 0%
  background-color: rgba(0, 0, 0, 0.3)

#menu
  width: 200px
  background-color: #2B7B8A
  height: 100%
  box-shadow: 10px 0px 30px 1px #113036
  transform: translateX(-200px)
  transition: transform 0.25s ease

  .nav-item
    padding: 10px 20px
    text-align: center
  .selected-item
    p
      color: #30DEFF
  .resume-container
    padding: 10px 20px
    // display: flex
    display: none
    justify-content: center
    align-items: center
    .resume
      cursor: pointer
      border: 1px solid #30DEFF
      padding: 5px 10px
      p
        color: #30DEFF

.close
  width: 100%
  height: 50px
  display: flex
  align-items: center
  .icon
    width: 32px
    height: 32px
    margin-left: 20px
</style>
